import { map, fold, sampleOn, fromAnimationFrames, dropRepeats, listen, send } from 'acto'
import { createMsgs, initialCreate, queryMsgs, initialQuery, streamMsgs, initialStream } from './reducers/thread'
import { userMsgs, initialUser,  } from './reducers/user'


// Msg :: { domNode: div, vDom: ['div', {}, []] }

// Msg -> State -> State
function updater (msg, state) {
    return msg[0](state, msg[1])
}

// Sig Msg -> State -> Sig State
function msgsToState (msgs) {
    return fold(updater, msgs.value, msgs)
}

// Sig State -> Sig State
function optimiseState (sig) {
    return dropRepeats(sampleOn(sig, fromAnimationFrames()))
}


// Sig { key: State }
export default optimiseState(map(
    (createState, queryState, streamState, userState) => ({
        create:     createState,
        query:      queryState,
        stream:     streamState,
        user:       userState
    }),
    msgsToState(createMsgs),
    msgsToState(queryMsgs),
    msgsToState(streamMsgs),
    msgsToState(userMsgs)
))



