import { init as initFirebase } from './firebase/thread'
import { map, listen, send } from 'acto'
import store from './store'
import mount from './utils/mount'
import ThreadStream from './views/ThreadStream'
import '../scss/app.scss'



listen(store, l => console.log(l))

// Sig VDom
const virtualDoms = map(ThreadStream, store)

mount(document.getElementById('app'), virtualDoms)

initFirebase()



