import { send } from 'acto'
import { 
    userMsgs, 
    setUid 
} from '../reducers/user'
import { 
    queryMsgs, 
    setLocation, 
    createMsgs, 
    resetCreate,
    streamMsgs,
    setStream,
    addThread,
    setThread,
    delThread 
} from '../reducers/thread'
import { unionO, mapToArr } from '../utils/object'
import store from '../store'


export function threadCreate ({ title, body, tags, location, creatorId }) {

    tags = tags && tags.split(/\s*,\s*/)

    const now = Date.now()
    const threadRef = firebase.database().ref('threads').push({
        title,
        body,
        tags,
        creatorId,
        location,
        created: now,
        modified: now,
        likes: 0,
        dislikes: 0
    })

    firebase.database().ref('created/user/' + creatorId + '/thread/' + threadRef.key).set(true)

    firebase.database().ref('tasks/thread/push').push({
        created: now,
        tId: threadRef.key,
        tags,
        location
    })
}

export function threadLike (uid, tId, creatorId, tags) {

    firebase.database().ref('likes/' + uid + '/' + tId).set(true)
    firebase.database().ref('threads/' + tId + '/likes').transaction(likes => (likes || 0) + 1)
    firebase.database().ref('tasks/tastes/train').push({ uid, tags, cat: 'like' })
}

export function threadDislike (uid, tId, creatorId, tags) {

    firebase.database().ref('dislikes/' + uid + '/' + tId).set(true)
    firebase.database().ref('threads/' + tId + '/likes').transaction(dislikes => (dislikes || 0) + 1)
    firebase.database().ref('streams/thread/' + uid + '/' + tId).remove()
    firebase.database().ref('tasks/tastes/train').push({ uid, tags, cat: 'like' })
}

export function threadStreamSub (uid, query) {

    firebase.database().ref('subscriptions/' + uid).set(query)
    firebase.database().ref('streams/thread/' + uid).on('value', data => {

        var threads = mapToArr((created, tId) => 
            firebase.database().ref('threads/' + tId).once('value'), 
            data.val()
        )

        Promise
            .all(threads)
            .then(threadData => {

                var stream = {}
                
                threadData.forEach(d => {
                    stream[d.key] = d.val()
                })

                send(streamMsgs, [setStream, stream])
            })
    })
}


export function getLocation (cback) {
    navigator.geolocation.getCurrentPosition(loc => {
        send(queryMsgs, [setLocation, { lat: loc.coords.latitude, lng: loc.coords.longitude }])
        cback()
    })
}


export function signIn () {
    if (!firebase.auth().currentUser) {
        firebase
            .auth()
            .signInAnonymously()
            .catch(err => console.error(err))
    }
}

export function signOut () {
    if (firebase.auth().currentUser) {
        firebase.auth().signOut()
    }
}


export function init () {

    firebase.initializeApp({
        apiKey:         'AIzaSyDn3JZRkZNqi0M6jI4NOlssDTJCyFbDAvc',
        authDomain:     'taste-a048c.firebaseapp.com',
        databaseURL:    'https://taste-a048c.firebaseio.com',
        storageBucket:  ''
    })

    firebase
        .auth()
        .onAuthStateChanged(user => {
            if (!user) { 
                signIn() 
            } else { 
                send(userMsgs, [setUid, user.uid])
                getLocation(() =>
                    threadStreamSub(user.uid, store.value.query))
            }
        })


    send(userMsgs, [setUid, false])    
}




