import { send } from 'acto'
import { mapToArr } from '../utils/object'
import { 
	queryMsgs, 
	setRadius,
	setAdventure,
	createMsgs,
	setTitle,
	setBody,
	setTags
} from '../reducers/thread'
import { 
	threadCreate, 
	threadStreamSub,
	threadLike,
	threadDislike 
} from '../firebase/thread'
import { Div, H2, H4, H5, P, Form, Input, Button, _ } from './domConsts'
import { unionO } from '../utils/object'
import './ThreadStream.scss'


function ThreadQuery ({ query: { location, radius, adventure }, user: { uid } }) {

    return [Form, { 
    			'class': 'threadQuery',
    			on: { submit(evt) {
					evt.preventDefault()
					threadStreamSub(uid, { location, radius, adventure }) 
				} } }, [

				[H2, _, ['Stream subscription filter']],

				[H5, _, ['location']],
				[P, _, [ JSON.stringify(location) ]],

		    	[H5, _, ['distance']],
		    	[Input, { 
		    		type: 'range', 
		    		min: 10, 
		    		max: 1000, 
		    		step: 10, 
		    		value: radius, 
		    		on: { change(evt) { send(queryMsgs, [setRadius, parseInt(evt.target.value, 10)]) } } }],
	    		[P, _, [radius]],
		    	
		    	[H5, _, ['conservative/adventurous']],
		    	[Input, { 
		    		type: 'range', 
		    		min: 0, 
		    		max: 100, 
		    		step: 10, 
		    		value: adventure,
		    		on: { change(evt) { send(queryMsgs, [setAdventure, parseInt(evt.target.value, 10)]) } } }],
	    		[P, _, [adventure]],

		    	[Button, _, ['Apply']]
		    ]]
}


function ThreadCreate ({ create: { title, body, tags, location }, user: { uid } }) {

	return [Form, { 
				'class': 'threadCreate',
				on: { submit(evt) {
					evt.preventDefault()
					threadCreate({ title, body, tags, location, creatorId: uid }) 
				} } }, [

				[H2, _, ['Create thread']],

				[Input, { 
					type: 'text', 
					placeholder: 'title', 
					value: title, 
					on: { keyup(evt) { send(createMsgs, [setTitle, evt.target.value]) } } }],

				[Input, { 
					type: 'text', 
					placeholder: 'body', 
					value: body, 
					on: { keyup(evt) { send(createMsgs, [setBody, evt.target.value]) } } }],

				[Input, { 
					type: 'text', 
					placeholder: 'tags (comma separated)', 
					value: tags, 
					on: { keyup(evt) { send(createMsgs, [setTags, evt.target.value]) } } }],

				[Button, _, ['Create thread']]
			]]
}


function ThreadItem ({ uid, tId, title, body, tags, location, creatorId, time }) {

	return [Div, { 'class': 'threadItem' }, [
				[P, {}, [tId]],
				[H4, _, [title]],
				[P, _, [body]],
				[P, _, [tags.join(', ')]],
				[Button, { on: { click(evt) { threadLike(uid, tId, creatorId, tags) } } }, ['Like']],
				[Button, { on: { click(evt) { threadDislike(uid, tId, creatorId, tags) } } }, ['Dislike']]
			]]
}


function ThreadList ({ items, uid }) {

	return ['.threadList', { 'class': 'threadList' }, [
				[H2, _, ['Thread list']],
				[Div, _, items.map(item => [ThreadItem, unionO(item, { uid })])]
			]]
}


export default function ThreadStream ({ query, stream, create, user }) {

	const streamItems = mapToArr((thread, tId) => unionO({ key: tId, tId }, thread), stream)

    return [Div, _, [

	    		[Div, { 'class': 'threadStream' }, [
			        [ThreadQuery, { query, user }],
			        [ThreadList, { items: streamItems, uid: user.uid }],
			        [ThreadCreate, { create, user }]
			    ]],

    			[Div, { 'class': 'threadUser' }, [user.uid ? 'Logged in as: ' + user.uid : 'Not logged in']]
			]]
}



