import { create } from 'acto'
import { unionO, withoutO } from '../utils/object'


// ---------- query ---------- 

export const initialQuery = {
    location:   {},
    radius:     100,
    adventure:  0,
    count:      30,
    from:       null,
    to:         null
}

export const queryMsgs  = create(initialQuery)

export function setRadius ({ location, adventure }, radius) {
    return { location, radius, adventure }
}

export function setAdventure ({ location, radius }, adventure) {
    return { location, radius, adventure }
}

export function setLocation ({ radius, adventure }, location) {
    return { location, radius, adventure }
}



// ---------- create ---------- 

export const initialCreate = {
    title:      '',
    body:       '',
    tags:       [],
    location:   {}
}

export const createMsgs = create(initialCreate)

export function resetCreate () {
    return initialCreate
}

export function setTitle ({ body, tags, location }, title) {
    return { title, body, tags, location }
}

export function setBody ({ title, tags, location }, body) {
    return { title, body, tags, location }
}

export function setTags ({ title, body, location }, tags) {
    return { title, body, tags, location }
}



// ---------- stream ---------- 

export const initialStream = {}

export const streamMsgs = create(initialStream)

export function setStream (stream, data) {
    return data
}

export function addThread (stream, { key, val }) {
    return unionO(stream, { [key]: val })
}

export function setThread (stream, { key, val }) {
    return unionO(stream, { [key]: val })
}

export function delThread (stream, { key }) {
    return withoutO(stream, key)
}




