
export function mapToArr (f, obj) {
	var res = []
	for (var p in obj) {
		if (obj.hasOwnProperty(p)) { res.push(f(obj[p], p)) }
	}
	return res
}


export function firstO (f, obj) {
	var p
	for (p in obj) {
		if (obj.hasOwnProperty(p) && f(obj[p], p)) {
			return obj[p] 
		}
	}
}

export function filterO (f, obj) {
	var res = {}
	var p
	for (p in obj) {
		if (obj.hasOwnProperty(p) && f(obj[p], p)) {
			res[p] = obj[p] 
		}
	}
	return res
}

export function mapO (f, obj) {
	var res = {}
	var p
	for (p in obj) {
		if (obj.hasOwnProperty(p)) {
			res[p] = f(obj[p], p)
		}
	}
	return res
}

export function foldO (f, seed, obj) {
	var res = seed
	var p
	for (p in obj) {
		if (obj.hasOwnProperty(p)) {
			res = f(obj[p], p, res)
		}
	}
	return res
}

export function anyO (f, obj) {
	var p
	for (p in obj) {
		if (obj.hasOwnProperty(p) && f(obj[p], p)) return true
	}
	return false
}

export function headO (obj) {
	var p
	for (p in obj) {
		if (obj.hasOwnProperty(p)) return obj[p]
	}
}

export function toArray (obj) {
	var res = []
	var p
	for (p in obj) {
		if (obj.hasOwnProperty(p)) {
			res.push(obj[p])
		}
	}
	return res
}

export function withoutO (obj, id) {
	var res = {}
	var p
	for (p in obj) {
		if (obj.hasOwnProperty(p) && p !== id) {
			res[p] = obj[p]
		}
	}
	return res
}

export function unionO (...objs) {
	var res = {}
	var p
	objs.forEach(obj => {
		if (typeof obj !== 'undefined' && obj !== null) {
			for (p in obj) {
				if (obj.hasOwnProperty(p)) {
					res[p] = obj[p]
				}
			}
		}
	})
	return res
}


